import React from 'react';
import './Input.css';

function Input({ getDate, getSystolic, getDiastolic, addReading, changeDisplay }) {
    return (
    	<div className="input-box">
    		<div>
	    		<label htmlFor="date">Date</label>
	    		<input 
	    			type="date" 
	    			placeholder="reading" 
	    			name="systolic"
	    			onChange={getDate}
	    		/>
    		</div>
    		<div>
	    		<label htmlFor="systolic">Systolic</label>
	    		<input 
	    			type="text" 
	    			placeholder="reading" 
	    			name="systolic" 
	    			onChange={getSystolic}
	    		/>
    		</div>
    		<div>
	    		<label htmlFor="diastolic">Diastolic</label>
	    		<input 
	    			type="text" 
	    			placeholder="reading" 
	    			name="biolic" 
	    			onChange={getDiastolic}
	    		/>
    		</div>
    		<button
    			onClick={addReading}
    			className="grow"
    		>Submit</button>

    		<div className="ma2">
	        	<button 
	        		className="grow active"
	        		onClick={changeDisplay}
	        	>Readings</button>
	        	<button 
	        		className="grow"
	        		onClick={changeDisplay}
	        	>Graph</button>
	        </div>
    	</div>
    );
}

export default Input;
