import React from 'react';
import './Readings.css';
import arrowUp from './arrow-up.png';
import arrowDown from './arrow-down.png';

function Readings({ readings }) {
    return (
    	<div className="reading-box ma5">
    		{
    			readings.map((reading, index) => {
    				return (
    					<div className="box shadow-2 grow" key={index}>
			    			<p>{reading[0]}</p>
				    		<h3>{reading[3]}<img src={arrowUp} alt="up" /></h3>
				    		<h3>{reading[4]}<img src={arrowDown} alt="down" /></h3>
				    	</div>
    				)
    			})
    		}
    	</div>
    );
}

export default Readings;
