import React from 'react';
import Chart from 'react-google-charts';
import './Graph.css';

function Graph({ readings, count }) {
    const heading = [['Date', '', '', 'Systolic', 'Diastolic']];
    let data = heading.concat(readings);
    let width = 600 < (count * 50) ? (count * 50) : 600;

    return (
        <div className="graph-container">
            <Chart
                width={`${width > window.innerWidth ? window.innerWidth : width}px`}
                height={'300px'}
                chartType="LineChart"
                loader={<div>Loading Chart</div>}
                data={data}
                options={
                    {
                        hAxis: {
                        title: 'Time',
                    },
                    vAxis: {
                        title: 'BP Readings',
                    },
                }}
                rootProps={{ 'data-testid': '1' }}
            />
        </div>
    );
}

export default Graph;