import React, {Component} from 'react';
import './App.css';
import Input from './components/Input/Input';
import Readings from './components/Readings/Readings';
import Graph from './components/Graph/Graph';

class App extends Component {
  constructor() {
    super();

    this.state = {
      date: '',
      systolic: 0,
      diastolic: 0,
      display: 'readings',
      readings: []
    }
  }

  getReading = (event) => {
    return event.target.value;
  }

  getDate = (event) => {
    this.setState({ date: this.getReading(event)});
  }

  getSystolic = (event) => {
    this.setState({ systolic: this.getReading(event)})
  }

  getDiastolic = (event) => {
    this.setState({ diastolic: this.getReading(event)})
  }

  addReading = () => {
    let { date, systolic, diastolic } = this.state;
    if(date && systolic && diastolic) {
      let readings = this.state.readings;
      let reading = [];

      reading.push(date);
      reading.push(120);
      reading.push(80);
      reading.push(Number(systolic));
      reading.push(Number(diastolic));

      readings.push(reading);

      this.setState({ readings });

      if(localStorage.readings) {
        localStorage.readings += reading + '||';
      }else {
        localStorage.readings = reading + '||';
      }
    }
  }

  changeDisplay = (event) => {
    event.target.parentElement.children[0].classList.remove('active');
    event.target.parentElement.children[1].classList.remove('active');
    event.target.classList.add('active');

    if(event.target.innerHTML === 'Readings') {
      this.setState({ display: 'readings'});
    }

    if(event.target.innerHTML === 'Graph') {
      this.setState({ display: 'graph'});
    }
  }

  getReadingsFromLocal = () => {
    let localData = localStorage.readings.split('||');
    localData.pop();

    let readings = localData.map(data => {
      let arr = data.split(',');
      let reading = [];

      reading.push(arr[0]);
      reading.push(Number(arr[1]));
      reading.push(Number(arr[2]));
      reading.push(Number(arr[3]));
      reading.push(Number(arr[4]));

      return reading;
    });

    this.setState({ readings });
  }

  componentDidMount() {
    if(localStorage.readings) {
      this.getReadingsFromLocal();
    }
  }

  render() {
          if(this.state.display === 'graph'){
            return (
              <div className="App">
                <h1 className="ma0">BP Reading</h1>
                <Input 
                  getDate={this.getDate}
                  getDiastolic={this.getDiastolic}
                  getSystolic={this.getSystolic}
                  addReading={this.addReading}
                  changeDisplay={this.changeDisplay}
                />
                <Graph 
                  readings={this.state.readings}
                  count={this.state.readings.length}
                />
              </div>
            );
          }
          else if(this.state.display === 'readings'){
            return (
              <div className="App">
                <h1 className="ma0">BP Reading</h1>
                <Input 
                  getDate={this.getDate}
                  getDiastolic={this.getDiastolic}
                  getSystolic={this.getSystolic}
                  addReading={this.addReading}
                  changeDisplay={this.changeDisplay}
                />
                <Readings 
                  readings={this.state.readings}
                /> 
              </div>
            );
          }
  }
}

export default App;
